.contact-actions {
  display: flex;
}
.contact-actions > * {
  margin-right: 10px;
}
.company-contacts {
  margin-top: 40px;
}
.company-contacts .company-contacts-col {
  margin-top: 15px;
}
.company-contacts .company-contacts-col:hover .company-contacts-row-edit {
  opacity: 1;
}
.company-contacts .company-contacts-row-edit {
  opacity: 0;
  cursor: pointer;
  display: flex;
  width: 30px;
  height: 25px;
  justify-content: center;
  align-items: center;
}
.company-contacts .company-contacts-row-edit svg {
  color: #989CA8;
}
.company-contacts .company-contacts-row-title {
  font-weight: 500;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
  display: flex;
}
.company-contacts .company-contacts-row-title span {
  flex: 1;
}
.company-contacts .company-contacts-row {
  font-weight: 500;
  font-size: 14px;
  color: #989CA8;
  letter-spacing: 0;
}
